// Base style (tag only)
* {
  box-sizing: border-box;

  &:before,
  &:after {
    box-sizing: border-box;
  }
}

html {
  font-size: 62.5%;
}

html,
body {
  @extend %noIndent;
}

body {
  background: $base-bg;
  color: $base-text;
}

a {
  background-color: transparent;
  color: $base-link;
  text-decoration: none;

  &:hover, &:focus, &:active {
    color: darken($base-link, 10%);
    outline: 0;
  }
}

img {
  max-width: 100%;
  vertical-align: middle;
  height: auto;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

